#Dialog {

  position: absolute;
  left: -100%;
  bottom: 50%;
  transform: translateY(50%);
  width: 90%;
  max-width: 500px;
  border: solid var(--ast-global-color-0);
  border-width: $space * 0.5 0 0 0;
  border-radius: 0 $space * 2 0 0 !important;
  transition-property: left;
  transition-duration: 0.5s;
  transition-delay: 0.5s;
  visibility: hidden;

  > h2 {

    position: absolute;
    font-size: 2.5rem;
    line-height: 1em;
    text-shadow: 0 0 1px #000;
    top: -0.86em;
    z-index: 1;
  }

  .Slide {

    z-index: 0;
    width: 100%;
    height: 100%;
    
    .wp-block-cover__background {

      display: none;
    }

    .wp-block-cover__inner-container {

      height: 100%;
    }

    h2, h3 {

      top: -1.4em;
      opacity: 0;
      text-shadow: 0 0 1px #000;
      position: relative; 
        
      transition-property: opacity, top;
      transition-duration: 0.5s;
      transition-delay: 0.3s;

      a {

        text-shadow: 0 0 1px #000;
      }    
    }

    h2 {

      line-height: 0.55em;
      margin-block-end: 0.05em;
      z-index: 1;  
    }

    h3 {

      margin-bottom: 0;
      z-index: 2;
    }

    p {

      transition-property: opacity, filter;
      transition-duration: 0.8s;
      transition-delay: 0.5s;
      z-index: 2;
      opacity: 0;
      filter: blur(12px);
    }

    .wp-block-buttons {    

      min-height: $space * 6;
      width: 100%;
      z-index: 2;
      cursor: pointer;
      visibility: hidden;

      transition-property: left;
      transition-duration: 0.8s;
      transition-delay: 1.5s;
    }
  }  

  &.Calculated {    
    
    left: 0;
    visibility: visible;

    .Slide {

      position: absolute;
      top: 0;
      left: 0;
      display: none;
      
      .wp-block-buttons { 

        position: absolute;
        bottom: 0; 
        left: -100%;
        visibility: visible;
      }

      &.Current {

        opacity: 1;
        left: 0; 
        display: block;     

        h2, h3 {

          top: 0;
          opacity: 1;
        }

        p {

          opacity: 1;
          filter: blur(0px);
        }
      
        .wp-block-buttons { 

          left: 0;
        }
      }
    }
  }

  .wp-block-button.common {

    @include flexcenter();
    overflow: hidden;
    border-radius: 50%;

    .wp-block-button__link {
      
      text-indent: 2000px;
      width: $space * 6;
      height: $space * 6;
      padding: 0;
    }

    &.home {

      .wp-block-button__link {

        @include bgimage("~assets/images/home-alt.svg");
        background-size: 60%;
      }
    }

    &.root {

      .wp-block-button__link {

        @include bgimage("~assets/images/arrow-circle.svg");
        background-size: 60%;
      }
    }
  }
}